body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
    --standard-margin: 20px;
    --background-card:  #f4f4f4;
}

blockquote, p, li, h1, h2, h3, h4{
    color: slategray;
}

h1{
    font-family: sans-serif;
    font-size: 3rem;
    font-weight: 100;
    letter-spacing: 0.2rem;
    margin: 20px 20px 30px 50px;

}
h2{
    font-size: 1.5rem;
    font-weight: 100;
    margin-top: 10px;
    margin-bottom: 10x;
    text-transform: uppercase;
}
h3{
    font-size: 1.5rem;
    font-weight: 100;
    margin-top: 10px;
    margin-bottom: 10x;
}

h4{
    font-size: 1.0rem;
    text-transform: uppercase;
    font-weight: 100;
    margin-top: 10px;
    margin-bottom: 10x;
}


p{
    font-size: 0.9rem;
    font-weight: 100;
    line-height: 1.4rem;
    text-align: justify;
}

a.button {
    padding: 6px 20px;
    -webkit-text-decoration: none;
    text-decoration: none;
    font-weight: 100;
    color: #333;
    letter-spacing: 0.2rem;
    border: 0.09rem solid #ccc;
}
a.read-all-button{
    font-size: 0.8rem;
    padding: 3px 10px;
    text-decoration: none;
    color: #333;
    letter-spacing: 0.2rem;
}

blockquote {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
    flex-direction: column;
    font-size: 0.9rem;
    font-weight: 100;
    line-height: 1.5rem;
    text-align: justify;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
  pointer-events: none;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

